/* border 相关 */
.border-extend,
.border-left:after,
.border-right:after,
.border-top:after,
.border-bottom:after,
.border-bottom-dash:after {
  content: ' ';
  position: absolute;
  background: #D8D9DC;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.border-left {
  position: relative;
}
.border-left:after {
  left: 0;
  top: 0;
  width: 1px;
  height: 200%;
  -webkit-transform-origin: top;
          transform-origin: top;
}
.border-right {
  position: relative;
}
.border-right:after {
  right: 0;
  top: 0;
  width: 1px;
  height: 200%;
  -webkit-transform-origin: top;
          transform-origin: top;
}
.border-top {
  position: relative;
}
.border-top:after {
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  -webkit-transform-origin: left;
          transform-origin: left;
}
.border-bottom {
  position: relative;
}
.border-bottom:after {
  left: 0;
  bottom: 0;
  width: 200%;
  height: 1px;
  -webkit-transform-origin: left;
          transform-origin: left;
}
.border-bottom-dash {
  position: relative;
}
.border-bottom-dash:after {
  background: -webkit-gradient(linear, left top, right top, from(#D8D9DC), color-stop(0.5rem, #D8D9DC), color-stop(0.5rem, transparent), to(transparent));
  background: linear-gradient(to right, #D8D9DC, #D8D9DC 0.5rem, transparent 0.5rem, transparent);
  background-size: 1rem 100%;
  left: 0;
  bottom: 0;
  width: 200%;
  height: 1px;
  -webkit-transform-origin: left;
          transform-origin: left;
}
.border {
  position: relative;
}
.border:after {
  content: ' ';
  border: 1px solid #D8D9DC;
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transform-origin: left top;
          transform-origin: left top;
}
.border-theme:after {
  border-color: #9cebbf !important;
}
.no-border [class*='border-']:after {
  height: 0 !important;
}
.nowrap {
  white-space: nowrap;
}
